.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
    animation: App-logo-spin infinite 20s linear;
  }
}

.c-header {
  border-bottom: #e3e4e5 solid 1px;
  background-color: #edca1fff;
  height: 100px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  padding: 1.25rem;
  gap: 10px;
}

.c-header-wrapper-text {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  gap: 7px;
}

@media (min-width: 600px) {
  .c-header {
    justify-content: start;
    padding-left: 40px;
    gap: 20px;
  }
}

.header__text {
  text-transform: capitalize;
}

.separation {
  height: 4.125rem;
  width: 2.5px;
  background-color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.c-header__image {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: 4.32rem;
  margin-left: 20px;
}

.c-input-search {
  padding-top: 20px;
  display: flex;
  gap: 20px;
  justify-content: space-between;
}

.c-input-search__input {
  width: 80%;
  height: 50px;
  border-radius: 20px;
  padding: 15px;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.c-input-search__button {
  padding-top: 100px;
  width: 200px;
  background-color: #edca1fff;
  color: #676767;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.first-div {
  height: 4.32rem;
  background-color: #020773ff;
  width: 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.second-div {
  height: 4.32rem;
  display: flex;
  flex-direction: column;
  width: 20px;
}
.yellow-div {
  background-color: #fcbf01ff;
  height: 100%;
}
.red-div {
  background-color: #ad1618ff;
  height: 1.7rem;
}

.point {
  position: relative;
  display: block;
  color: #fff000ff;
  height: 0.4rem;
  width: 0.4rem;
  border-right: 100px solid transparent;
  border-bottom: 70px solid #fff000ff;
  border-left: 100px solid transparent;
  transform: rotate(35deg) scale(0.05);
  top: 0;
}

.point::before {
  border-bottom: 80px solid #fff000ff;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  position: absolute;
  height: 0.4rem;
  width: 0.4rem;
  top: -45px;
  left: -65px;
  display: block;
  content: "";
  transform: rotate(-35deg);
}

.point::after {
  position: absolute;
  display: block;
  color: #fff000ff;
  top: 3px;
  left: -105px;
  height: 0.4rem;
  width: 0.4rem;
  border-right: 100px solid transparent;
  border-bottom: 70px solid #fff000ff;
  border-left: 100px solid transparent;
  transform: rotate(-70deg);
  content: "";
}

.black {
  color: black;
}

.container {
  padding: 20px;
}

li {
  list-style: none;
  text-align: left;
  margin: 0;
  padding: 0;
}
ul {
  padding: 0;
}

.c-image {
  background-image: url("../src/assets/images/cris.jpeg");
  background-position: inherit;
  background-size: cover;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.c-button {
  background-color: #edca1fff;
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  font-size: 14px;
  font-weight: 500;
  height: 40px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  outline: none;
  padding: 10px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: color 100ms;
  vertical-align: baseline;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100%;
}

.c-button:hover,
.c-button:focus {
  background-color: #020773ff;
}

